#border-img {
    border: 10px solid transparent;
    padding: 15px;
    background-image: url(./thanks_background.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

#thanks{
    margin-top: 70px;
}
.thanks {
    font-size: 5em;
    font-family: 'Questrial', sans-serif;
    text-align: center;
}

.thanks-titles {
    color: rgb(228, 134, 58);
    font-weight: bold;
    color: #3d5e79;
    font-size: 18px;
    font-weight: bold;
}

.thanks-section {
    margin-top: 2em;
}

.thanks-message {
    font-size: 16px;
    margin-top: 1em;
}


@media only screen and (max-width: 800px) {
  
    .thanks {
        font-size: 3em;
        font-family: 'Questrial', sans-serif;
        text-align: center;
    }

    .thanks-paragraph {
        font-size: 16px;
    }

    .thanks-titles {
        font-weight: bold;
        color: #3D5E79;
    }
  }

