body {
    font-family: 'Open Sans', sans-serif;
}

.student-card {
    display: flex;
    color: #ffffff;
    flex-direction: column;
    background-color: #285943;
    height: auto;
    width: 360px;
    margin: 1em;
    border-radius: 1em;
    text-align: center;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    -webkit-transition: 0.3s ease;
    transition: 0.3s ease;

}
.student-card:hover{
  -webkit-transition: 0.3s ease;
  transition: 0.3s ease;
  box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.418);
  background-color: #397c5e;
}

.card-content {
  padding: 0 .5em .5em .5em;
  
}

.card-content h3 {
  margin-top: 0;
  margin-bottom: .5em;
  font-weight: bold;
  color: #285943;
}

.card-content p {
  font-size: 20px;
  color: white;

}

.card-content .btn-resume, .card-content .btn-capstone {
  color: white;
  margin: 8px;
}

.student-serious-photo, .student-fun-photo {
  width: 285px;
  height: 428px;
  border-radius: 1em;
  z-index: 2;

}

.card-fx {
  /* width: 130px;
  height: 195px; */
  position: relative;
  display: inline-block;
  margin: 10px;

}
.card-fx .student-fun-photo {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 99;
}
.card-fx:hover .student-fun-photo {
  display: inline;
}

.icon-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;
  -webkit-transition: 0.5 ease;
  transition: 0.5 ease;
}

.icons {
  margin: 7px;
  padding-left: 5px;
  padding-right: 5px;
  font-size: 40px;
  /* background-color: #285943; */
  color:rgb(241, 239, 239);
  -webkit-transition: 0.5 ease;
  transition: 0.5 ease;
}

.icons:hover {
  margin: 7px;
  opacity: 0.8;
  -webkit-transition: 0.5 ease;
  transition: 0.5 ease;
  color: rgb(148, 146, 146);
}

.student-name {
  color: rgb(241, 239, 239);
  /* font-family: 'Open Sans', sans-serif; */
  font-family: 'Questrial', sans-serif;
  font-weight: bold;
  /* text-transform: uppercase; */

}

